import React,{useEffect,useState} from "react";
import "./main-area.css";
import {Card,Row,Col} from "react-bootstrap"

import {useHistory} from 'react-router-dom'
import axios from "axios";
function Globalmainarea(props) {
    const [globaldata, setGlobaldata] = useState(null)
    console.log(globaldata);
    const history=useHistory()
    useEffect(() => {
      axios.get("https://disease.sh/v3/covid-19/all").then((response)=>{
        // console.log(response.data);
        setGlobaldata(response.data)
      })
    }, [null])
    return (
        <div>
             <div className="main-area">
        <h1>Statistics</h1>
        <div className="btn-area">
          <button onClick={()=>{
              history.push('/')
          }} className={`country-btn ${props.value == "my-country" ? "btnz-active":"" }`} >My Country</button>

          <button onClick={()=>{
              history.push('/global')
          }} className={`global-btn ${props.value == "global" ? "btnz-active" : ""} `} >Global</button>
        </div>
        <div className="display-area col-xs-12">

    <Row xs={2} md={4} className="g-4">
  
    <Col>
      <Card className="cards-main Affected">
        <Card.Body>
        <h5>Affected</h5>
         <h2>{globaldata ? globaldata.cases : "check update" }</h2>
        </Card.Body>
      </Card>
    </Col>

    <Col>
      <Card className="cards-main Death">
        <Card.Body>
        <h5>Death</h5>
         <h2>{globaldata ? globaldata.deaths : "check update" }</h2>
        </Card.Body>
      </Card>
    </Col>

    <Col>
      <Card className="cards-main recover ">
        <Card.Body>
        <h5>Recovered</h5>
         <h2>{globaldata ? globaldata.recovered : "check update" }</h2>
        </Card.Body>
      </Card>
    </Col>
    <Col>

      <Card className="cards-main active ">
        <Card.Body>
        <h5>Active</h5>
         <h2>{globaldata ? globaldata.active : "check update" }</h2>
        </Card.Body>
      </Card>
    </Col>
  
</Row>
          
        </div>

        

      </div>
        </div>
    )
}

export default Globalmainarea
