import React from 'react'

import Globaldata from './Components/datatable/Globaldata'
import Globalmainarea from './Components/main-area/Globalmainarea'
import Navbar from './Components/nav/Navbar'

function Globalpage() {
    return (
        <div>
            <Navbar/>
            <Globalmainarea value={"global"}/>
            <Globaldata headtxt={"Country Wise Data"}/>
        </div>
    )
}

export default Globalpage
