import React from "react";
import "./datatable.css";
import { useEffect, useState } from "react";
import axios from "axios";
import {Table} from 'react-bootstrap'

function Globaldata(props) {
  const [countriesdata, setCountriesdata] = useState([]);
  console.log(countriesdata);

  useEffect(() => {
    axios
      .get("https://corona.lmao.ninja/v2/countries")
      .then((response) => {
        setCountriesdata(response.data);
      });
  }, [null]);
  return (
    <div>
      <div className="statewise-area">
        <div className="statewise-child">
          <h1>{props.headtxt}</h1>

          <Table responsive className="table-dataz" >
            <thead>
              <tr>
                <th>Flag</th>
                <th>Country</th>
                <th>Affected</th>
                <th>Death</th>
                <th>Recovered</th>
                <th>Active</th>
              </tr>
            </thead>
            <tbody>
                {countriesdata.map((item)=>{
                   return <tr>
                   <td><img src={`https://www.countryflags.io/${item.countryInfo.iso2}/flat/24.png`}/></td>
                   <td>{item.country}</td>
                   <td>{item.cases}</td>
                   <td>{item.deaths}</td>
                   <td>{item.recovered}</td>
                   <td>{item.active}</td>
                 </tr>
                })}
              
              
            </tbody>
          </Table>
        </div>
      </div>
    </div>
  );
}

export default Globaldata;
