import './App.css';
import Homepage from './Homepage';
import {BrowserRouter as Router,Route,Switch} from "react-router-dom";
import Globalpage from './Globalpage';

function App() {
  return (
      <Router>
    <div className="App">
        <Switch>

      <Route exact path="/">
         <Homepage/>
      </Route>  
      <Route path="/global">
         <Globalpage/>
      </Route>  
        </Switch>
    </div>
      </Router>
  );
}

export default App;
